@Component({
	name: 'Home'
})
export default class Home extends Vue {
	location = {
		longitude: 116.3974770000,
		latitude: 39.9086920000
	};
	controls = [{
		id: 1,
		position: {
			left: 5,
			top: 180,
			width: 30,
			height: 30
		},
		iconPath: '/static/logo.png',
		clickable: true
	}];
	showLocation = false;
	scale = 13;
	showCompass = true;
	enable3D = true;
	enableOverlooking = true;
	enableOverlooking = true;
	enableZoom = true;
	enableScroll = true;
	enableRotate = true;
	enableSatellite = false;
	enableTraffic = false;
	polyline = [];
	markers = [];
	polygons = [];
	circles = [];
	includePoints = [];
	rotate = 0;
	skew = 0;

	changeScale() {
		this.scale = this.scale == 9 ? 15 : 9;
	};
	changeRotate() {
		this.rotate = this.rotate == 90 ? 0 : 90;
	};
	changeSkew() {
		this.skew = this.skew == 30 ? 0 : 30;
	};
	enableThreeD(e) {
		this.enable3D = e.detail.value;
	};
	changeShowCompass(e) {
		this.showCompass = e.detail.value;
	};
	changeEnableOverlooking(e) {
		this.enableOverlooking = e.detail.value;
	};
	changeEnableZoom(e) {
		this.enableZoom = e.detail.value;
	};
	changeEnableScroll(e) {
		this.enableScroll = e.detail.value;
	};
	changeEnableRotate(e) {
		this.enableRotate = e.detail.value;
	};
	changeEnableSatellite(e) {
		this.enableSatellite = e.detail.value;
	};
	changeEnableTraffic(e) {
		this.enableTraffic = e.detail.value;
	};
	addMarkers() {
		this.markers = testMarkers;
	};
	addPolyline() {
		this.polyline = testPolyline;
	};
	addPolygons() {
		this.polygons = testPolygons;
	};
	addCircles() {
		this.circles = testCircles;
	};
	includePoint() {
		this.includePoints = testIncludePoints;
	};
	handleGetCenterLocation() {
		this.map.getCenterLocation({
			success: ret => {
				console.log(JSON.stringify(ret));
				uni.showModal({
					content: JSON.stringify(ret)
				})
			}
		})
	};
	handleGetRegion() {
		this.map.getRegion({
			success: ret => {
				console.log(JSON.stringify(ret));
				uni.showModal({
					content: JSON.stringify(ret)
				})
			}
		})
	};
	handleTranslateMarker() {
		this.map.translateMarker({
			markerId: 1,
			destination: {
				latitude: 39.989631,
				longitude: 116.481018
			},
			duration: 2000
		}, ret => {
			console.log(JSON.stringify(ret));
			uni.showModal({
				content: JSON.stringify(ret)
			})
		});
	};
	maptap(e) {
		uni.showModal({
			content: JSON.stringify(e)
		})
	};
	onmarkertap(e) {
		uni.showModal({
			content: JSON.stringify(e)
		})
	};
	oncontroltap(e) {
		uni.showModal({
			content: JSON.stringify(e)
		})
	};
	oncallouttap(e) {
		uni.showModal({
			content: JSON.stringify(e)
		})
	};
	onupdated(e) {
		console.log(JSON.stringify(e))
	};
	onregionchange(e) {
		console.log(JSON.stringify(e));
	};
	onpoitap(e) {
		uni.showModal({
			content: JSON.stringify(e)
		})
	};
}
